@use "sass:map";

.contact__container {
  width: 896px;
  margin: 155px auto 59px;
  display: flex;
}
/**** contact info  ****/
.contact__information {
  display: flex;
  flex-direction: column;
}
.contact__headline {
  font-family: $font-raleway;
  font-weight: 400;
  font-size: 3.0625rem;
  line-height: 3.5625rem;
  color: map.get($colors, color-black);
  margin-bottom: 22px;
}
.contact__text {
  font-family: $font-open-sans;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.375rem;
  color: map.get($colors, color-black);
  margin-bottom: 40px;
}
.contact__details {
  display: flex;
}
.contact__email,
.contact__phone {
  font-family: $font-open-sans;
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.375rem;
  color: map.get($colors, color-grey);
}
.contact__email {
  margin-right: 20px;
}
/**** contact form  ****/
.form {
  max-width: 440px;
  display: flex;
  flex-direction: column;
  margin-left: 113px;
}
.form__field {
  display: flex;
  flex-direction: column;
}
.form__field:nth-child(2) {
  margin-top: 42px;
  margin-bottom: 30px;
}
.form__label {
  font-family: $font-open-sans;
  font-weight: 700;
  font-size: 0.6875rem;
  line-height: 0.9375rem;
  letter-spacing: 0.205rem;
  color: map.get($colors, color-primary);
  text-align: left;
}
.form__input {
  height: 43px;
  border: none;
  border-bottom: 1px solid map.get($colors, color-checkbox-text);
}
/**** contact checkbox  ****/
.form__permission {
  margin-bottom: 20px;
}
.form__checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.form__checkbox {
  cursor: pointer;
  display: flex;
}
.checkbox {
  height: 24px;
  flex-shrink: 0;
  flex-basis: 24px;
  border: 1px solid map.get($colors, color-checkbox);
  border-radius: 4px;
  margin-right: 10px;
}
.form__checkbox input:checked + .checkbox {
  background-image: url("../assets/Checkmark.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.checkbox__text {
  font-family: $font-open-sans;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 1.0625rem;
  text-align: left;
  color: map.get($colors, color-checkbox-text);
}
/**** contact button  ****/
.contact__button {
  display: inline-block;
  padding: 20px 97px;
  cursor: pointer;
  border-radius: $button-radius;
  box-shadow: map.get($colors, color-primary-shadow) 5px 10px 14px;
  font-family: $font-open-sans;
  font-weight: 700;
  font-size: 0.8125rem;
  line-height: 1.125rem;
  color: map.get($colors, color-white);
  background-color: map.get($colors, color-primary);
  outline: none;
  border: none;
  align-self: flex-end;
}